import React, { useState, useEffect } from 'react'
import { Formik, Form, Field, FormikHelpers } from 'formik'
import * as yup from 'yup'
import { useTranslation } from 'react-i18next'
import { Container, Row, Col } from 'styled-bootstrap-grid'
import { usePrevious } from 'hooks'
import { ErrorResponse } from 'types'
import { Note } from 'components/common'
import { Button } from 'components/form/controls'
import { InputField } from 'components/form/fields'
import { ErrorMessage } from 'components/form/controls/common'
import { SetupPasswordParams } from 'modules/Auth/types'
import { AuthFormLayout } from 'modules/Auth/layouts'
import { FinalState, BackButton } from 'modules/Auth/components'
import authRoutes from 'modules/Auth/routing/routes'
import { ReactComponent as IconLock } from 'assets/images/i-lock.svg'
import { NoteWrapper, ErrorWrapper } from './SetupPasswordForm.styled'

type SetupPasswordFormProps = {
  onSubmit: (formData: SetupPasswordParams) => void
  loading?: boolean
  error?: ErrorResponse
}

const SetupPasswordForm: React.FC<SetupPasswordFormProps> = props => {
  const { onSubmit, loading, error } = props
  const prevLoading = usePrevious(loading)
  const [formikBag, setFormikBag] = useState<FormikHelpers<
    SetupPasswordParams
  > | null>(null)
  const [requestSuccessful, setSuccessState] = useState(false)

  useEffect(() => {
    if (prevLoading && !loading && formikBag) {
      formikBag.setSubmitting(false)
      if (!error) {
        setSuccessState(true)
      }
    }
  }, [formikBag, loading, prevLoading, error])

  const { t } = useTranslation('translations:setupPassword')

  const VALIDATION_SCHEMA = yup.object().shape<SetupPasswordParams>({
    password: yup
      .string()
      .required(t('translations:setupPassword.passwordRequired')),
    repeatPassword: yup
      .string()
      .oneOf(
        [yup.ref('password'), null],
        t('translations:setupPassword.passwordMatchError')
      )
      .required(t('translations:setupPassword.repeatPasswordRequired')),
  })

  const INITIAL_VALUES: SetupPasswordParams = {
    password: '',
    repeatPassword: '',
  }

  function handleSubmitForm(
    values: SetupPasswordParams,
    actions: FormikHelpers<SetupPasswordParams>
  ) {
    onSubmit(values)
    setFormikBag(actions)
  }

  function renderSuccess() {
    return (
      <FinalState
        title={t('translations:setupPassword.header1')}
        description={t('translations:setupPassword.allSet')}
        backText={t('translations:setupPassword.login')}
        backPath={authRoutes.login}
      />
    )
  }

  function renderForm() {
    return (
      <>
        <Row>
          <Col>
            <NoteWrapper>
              <Note
                title={t('translations:setupPassword.header1')}
                description={t('translations:setupPassword.header2')}
              />
            </NoteWrapper>
          </Col>
        </Row>
        {error && (
          <Row>
            <Col>
              <ErrorWrapper>
                <ErrorMessage error={error?.error} />
              </ErrorWrapper>
            </Col>
          </Row>
        )}
        <Formik
          validationSchema={VALIDATION_SCHEMA}
          initialValues={INITIAL_VALUES}
          onSubmit={handleSubmitForm}
          validateOnMount
          validateOnBlur
          validateOnChange
        >
          {({ isValid, isSubmitting }) => (
            <Form>
              <AuthFormLayout>
                <Field
                  title={t('translations:setupPassword.password')}
                  name='password'
                  type='password'
                  icon={<IconLock />}
                  component={InputField}
                />
                <Field
                  title={t('translations:setupPassword.repeatPassword')}
                  name='repeatPassword'
                  type='password'
                  icon={<IconLock />}
                  component={InputField}
                />
                <Button
                  fill
                  type='submit'
                  title={t('translations:setupPassword.savePassword')}
                  disabled={!isValid || isSubmitting}
                />
              </AuthFormLayout>
            </Form>
          )}
        </Formik>
      </>
    )
  }

  return (
    <Container fluid>
      <Row>
        <Col>
          <BackButton
            title={t('translations:setupPassword.backToLogin')}
            to={authRoutes.login}
          />
        </Col>
      </Row>
      {!requestSuccessful ? renderForm() : renderSuccess()}
    </Container>
  )
}

export default SetupPasswordForm
