import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from 'store'
import { AuthUserParams } from 'modules/Auth/types'
import { authenticateUser } from 'modules/Auth/slices/auth'
import LoginForm from 'modules/Auth/components/LoginForm'

type LoginContainerProps = {
  segmentId?: string
}

const LoginContainer: React.FC<LoginContainerProps> = () => {
  const dispatch = useDispatch()
  const { isLoading, error } = useSelector(({ auth }: RootState) => ({
    isLoading: auth.session.isLoading,
    error: auth.session.error,
  }))

  function onSubmit(formData: AuthUserParams) {
    dispatch(authenticateUser(formData))
  }

  return <LoginForm loading={isLoading} error={error} onSubmit={onSubmit} />
}

export default LoginContainer
