import React from 'react'
import Decorator, { Variant } from 'components/common/Decorator'
import { LayoutProps } from 'layouts/types'
import withLayout from 'layouts/withLayout'
import { Container, Content, Wrapper } from './AuthLayout.styled'

export type AuthLayoutSegments = 'header' | 'content' | 'footer'
type InternalLayoutProps = LayoutProps<AuthLayoutSegments>

const AuthLayout = ({ segments }: InternalLayoutProps) => {
  return (
    <Decorator variant={Variant.AuthLayout}>
      <Container>
        {segments?.header}
        <Content>
          <Wrapper>{segments?.content}</Wrapper>
        </Content>
        {segments?.footer}
      </Container>
    </Decorator>
  )
}

export default withLayout(AuthLayout)
