import React, { useState, useEffect } from 'react'
import { Formik, Form, Field, FormikHelpers } from 'formik'
import * as yup from 'yup'
import { useTranslation } from 'react-i18next'
import { Container, Row, Col } from 'styled-bootstrap-grid'
import { usePrevious } from 'hooks'
import { ErrorResponse } from 'types'
import { Note } from 'components/common'
import { Button } from 'components/form/controls'
import { InputField } from 'components/form/fields'
import { ErrorMessage } from 'components/form/controls/common'
import { ForgotPasswordParams } from 'modules/Auth/types'
import { AuthFormLayout } from 'modules/Auth/layouts'
import { FinalState, BackButton } from 'modules/Auth/components'
import authRoutes from 'modules/Auth/routing/routes'
import { ReactComponent as IconCompany } from 'assets/images/i-company.svg'
import { ReactComponent as IconUser } from 'assets/images/i-user.svg'
import { NoteWrapper, ErrorWrapper, Footer } from './ForgotPasswordForm.styled'

type ForgotPasswordFormProps = {
  onSubmit: (formData: ForgotPasswordParams) => void
  loading?: boolean
  error?: ErrorResponse
}

const ForgotPasswordForm: React.FC<ForgotPasswordFormProps> = props => {
  const { onSubmit, loading, error } = props
  const prevLoading = usePrevious(loading)
  const [formikBag, setFormikBag] = useState<FormikHelpers<
    ForgotPasswordParams
  > | null>(null)
  const [requestSuccessful, setSuccessState] = useState(false)

  useEffect(() => {
    if (prevLoading && !loading && formikBag) {
      formikBag.setSubmitting(false)
      if (!error) {
        setSuccessState(true)
      }
    }
  }, [formikBag, loading, prevLoading, error])

  const { t } = useTranslation('translations:forgottenPassword')

  const VALIDATION_SCHEMA = yup.object().shape<ForgotPasswordParams>({
    customerName: yup
      .string()
      .required(t('translations:forgottenPassword.companyRequired')),
    email: yup
      .string()
      .required(t('translations:forgottenPassword.emailRequired'))
      .email(t('translations:forgottenPassword.emailInvalid')),
  })

  const INITIAL_VALUES: ForgotPasswordParams = {
    customerName: '',
    email: '',
  }

  function handleSubmitForm(
    values: ForgotPasswordParams,
    actions: FormikHelpers<ForgotPasswordParams>
  ) {
    onSubmit(values)
    setFormikBag(actions)
  }

  function renderSuccess() {
    return (
      <FinalState
        title={t('translations:forgottenPassword.requestSent')}
        description={t('translations:forgottenPassword.checkEmail')}
        backText={t('translations:forgottenPassword.backToLogin')}
        backPath={authRoutes.login}
      />
    )
  }

  function renderForm() {
    return (
      <>
        <Row>
          <Col>
            <NoteWrapper>
              <Note
                title={t('translations:forgottenPassword.header1')}
                description={t('translations:forgottenPassword.header2')}
              />
            </NoteWrapper>
          </Col>
        </Row>
        {error && (
          <Row>
            <Col>
              <ErrorWrapper>
                <ErrorMessage error={error?.error} />
              </ErrorWrapper>
            </Col>
          </Row>
        )}
        <Formik
          validationSchema={VALIDATION_SCHEMA}
          initialValues={INITIAL_VALUES}
          onSubmit={handleSubmitForm}
          validateOnMount
          validateOnBlur
          validateOnChange
        >
          {({ isValid, isSubmitting }) => (
            <Form>
              <AuthFormLayout>
                <Field
                  title={t('translations:forgottenPassword.company')}
                  name='customerName'
                  icon={<IconCompany />}
                  component={InputField}
                />
                <Field
                  title={t('translations:forgottenPassword.email')}
                  name='email'
                  icon={<IconUser />}
                  component={InputField}
                />
                <Button
                  fill
                  type='submit'
                  title={t('translations:forgottenPassword.requestNewPassword')}
                  disabled={!isValid || isSubmitting}
                />
              </AuthFormLayout>
            </Form>
          )}
        </Formik>
        <Row>
          <Col>
            <Footer>{t('translations:forgottenPassword.footer')}</Footer>
          </Col>
        </Row>
      </>
    )
  }

  return (
    <Container fluid>
      <Row>
        <Col>
          <BackButton
            title={t('translations:forgottenPassword.backToLogin')}
            to={authRoutes.login}
          />
        </Col>
      </Row>
      {!requestSuccessful ? renderForm() : renderSuccess()}
    </Container>
  )
}

export default ForgotPasswordForm
