import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from 'store'
import { ForgotPasswordParams } from 'modules/Auth/types'
import { forgotPasswordRequest } from 'modules/Auth/slices/auth'
import { ForgotPasswordForm } from 'modules/Auth/components'

type ForgotPasswordContainerProps = {
  segmentId?: string
}

const ForgotPasswordContainer: React.FC<ForgotPasswordContainerProps> = () => {
  const dispatch = useDispatch()
  const { isLoading, error } = useSelector(({ auth }: RootState) => ({
    isLoading: auth.forgotPasswordRequest.isLoading,
    error: auth.forgotPasswordRequest.error,
  }))

  function onSubmit(formData: ForgotPasswordParams) {
    dispatch(forgotPasswordRequest(formData))
  }

  return (
    <ForgotPasswordForm loading={isLoading} error={error} onSubmit={onSubmit} />
  )
}

export default ForgotPasswordContainer
