import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from 'store'
import { SetupPasswordParams } from 'modules/Auth/types'
import { setupPasswordRequest } from 'modules/Auth/slices/auth'
import { SetupPasswordForm } from 'modules/Auth/components'

type SetupPasswordContainerProps = {
  segmentId?: string
}

const SetupPasswordContainer: React.FC<SetupPasswordContainerProps> = () => {
  const dispatch = useDispatch()
  const { isLoading, error } = useSelector(({ auth }: RootState) => ({
    isLoading: auth.setupPasswordRequest.isLoading,
    error: auth.setupPasswordRequest.error,
  }))

  function onSubmit(formData: SetupPasswordParams) {
    dispatch(setupPasswordRequest(formData))
  }

  return (
    <SetupPasswordForm loading={isLoading} error={error} onSubmit={onSubmit} />
  )
}

export default SetupPasswordContainer
