import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { Formik, Form, Field, FormikHelpers } from 'formik'
import * as yup from 'yup'
import { useTranslation } from 'react-i18next'
import { Container, Row, Col } from 'styled-bootstrap-grid'
import { usePrevious } from 'hooks'
import { ErrorResponse } from 'types'
import { Button } from 'components/form/controls'
import { InputField } from 'components/form/fields'
import { ErrorMessage } from 'components/form/controls/common'
import { AuthUserParams } from 'modules/Auth/types'
import { AuthFormLayout } from 'modules/Auth/layouts'
import authRoutes from 'modules/Auth/routing/routes'
import { ReactComponent as IconCompany } from 'assets/images/i-company.svg'
import { ReactComponent as IconUser } from 'assets/images/i-user.svg'
import { ReactComponent as IconPassword } from 'assets/images/i-lock.svg'
import { Header, ErrorWrapper, Footer } from './LoginForm.styled'

type LoginFormProps = {
  onSubmit: (formData: AuthUserParams) => void
  loading?: boolean
  error?: ErrorResponse
}

const LoginForm: React.FC<LoginFormProps> = props => {
  const { onSubmit, loading, error } = props
  const prevLoading = usePrevious(loading)
  const [formikBag, setFormikBag] = useState<FormikHelpers<
    AuthUserParams
  > | null>(null)

  useEffect(() => {
    if (prevLoading && !loading && formikBag) {
      formikBag.setSubmitting(false)
    }
  }, [formikBag, loading, prevLoading])

  const { t } = useTranslation('translations:login')

  const VALIDATION_SCHEMA = yup.object().shape<AuthUserParams>({
    customerName: yup
      .string()
      .required(t('translations:login.companyRequired')),
    userId: yup.string().required(t('translations:login.emailRequired')),
    password: yup.string().required(t('translations:login.passwordRequired')),
  })

  const INITIAL_VALUES: AuthUserParams = {
    customerName: 'dev',
    userId: 'sysadmin',
    password: 'admin',
  }

  function handleSubmitForm(
    values: AuthUserParams,
    actions: FormikHelpers<AuthUserParams>
  ) {
    onSubmit(values)
    setFormikBag(actions)
  }

  return (
    <Container fluid>
      <Row>
        <Col>
          <Header>
            <div>{t('translations:login.info')}</div>
            <div>{t('translations:login.start')}</div>
          </Header>
        </Col>
      </Row>
      {error && (
        <Row>
          <Col>
            <ErrorWrapper>
              <ErrorMessage error={error?.error} />
            </ErrorWrapper>
          </Col>
        </Row>
      )}
      <Formik
        validationSchema={VALIDATION_SCHEMA}
        initialValues={INITIAL_VALUES}
        onSubmit={handleSubmitForm}
        validateOnMount
        validateOnBlur
        validateOnChange
      >
        {({ isValid, isSubmitting }) => (
          <Form>
            <AuthFormLayout>
              <Field
                title={t('translations:login.company')}
                name='customerName'
                icon={<IconCompany />}
                component={InputField}
              />
              <Field
                title={t('translations:login.user')}
                name='userId'
                icon={<IconUser />}
                component={InputField}
              />
              <Field
                title={t('translations:login.password')}
                name='password'
                type='password'
                icon={<IconPassword />}
                component={InputField}
              />
              <Button
                fill
                type='submit'
                title={t('translations:login.login')}
                disabled={!isValid || isSubmitting}
              />
            </AuthFormLayout>
          </Form>
        )}
      </Formik>
      <Row>
        <Col>
          <Footer>
            <Link to={authRoutes.forgot}>
              {t('translations:login.forgotPassword')}
            </Link>
          </Footer>
        </Col>
      </Row>
    </Container>
  )
}

export default LoginForm
